import React, { useState, useEffect } from 'react';
import {
  obtenerProductos,
  crearProducto,
  actualizarProducto,
  eliminarProducto,
} from '../servicios/productos';

function GestionProductos() {
  const [productos, setProductos] = useState([]);
  const [productoForm, setProductoForm] = useState({
    name: '',
    tipo: '',
    presentacion: '',
    costo: 0,
    precio: 0,
    proveedor: '',
    fechaf: '',
    fechav: '',
  });

  useEffect(() => {
    obtenerProductos().then((res) => setProductos(res.data));
  }, []);

  const manejarCambio = (e) => {
    setProductoForm({ ...productoForm, [e.target.name]: e.target.value });
  };
  
  const actualizarListaProductos = () => {
    obtenerProductos().then((res) => setProductos(res.data));
  };

  const manejarSubmit = (e) => {
    e.preventDefault();

    const buttonValue = e.nativeEvent.submitter.value;

    switch (buttonValue) {
      case 'crear':
        crearProducto(productoForm).then(() => {
          actualizarListaProductos();
        });
        break;
      case 'actualizar':
        actualizarProducto(productoForm).then(() => {
          actualizarListaProductos();
        });
        break;
      case 'eliminar':
        eliminarProducto(productoForm).then(() => {
          actualizarListaProductos();
        });
        break;
      default:
        break;
    }
  };

  return (
    <div>
      <form onSubmit={manejarSubmit}>
        <input type="text" name="name" onChange={manejarCambio} placeholder='Nombre'/>
        <input type="text" name="tipo" onChange={manejarCambio} placeholder='Tipo'/>
        <input type="text" name="presentacion" onChange={manejarCambio} placeholder='Presentación'/>
        <input type="text" name="costo" onChange={manejarCambio} placeholder='Costo'/>
        <input type="text" name="precio" onChange={manejarCambio} placeholder='Precio'/>
        <input type="text" name="proveedor" onChange={manejarCambio} placeholder='Proveedor'/>
        <input type="date" name="fechaf" onChange={manejarCambio} placeholder='Fecha fabricación'/>
        <input type="date" name="fechav" onChange={manejarCambio} placeholder='Fecha vencimiento'/>
        <input type="submit" value="crear" />
        <input type="submit" value="actualizar" />
        <input type="submit" value="eliminar" />
      </form>
      {/* Lista de productos */}
      <ul>
        {productos.map((producto) => {
          const fechaF = new Date(producto.fechaf);
          const fechaFormateadaF = `${fechaF.getDate()}-${fechaF.getMonth() + 1}-${fechaF.getFullYear()}`;
          const fechaV = new Date(producto.fechav);
          const fechaFormateadaV = `${fechaV.getDate()}-${fechaV.getMonth() + 1}-${fechaV.getFullYear()}`;
          
          return (
            <li key={producto.name+producto.tipo+producto.proveedor}>
              {producto.name} - {producto.tipo} - {producto.presentacion} - {producto.costo} - {producto.precio} - {producto.proveedor} - {fechaFormateadaF} - {fechaFormateadaV}
            </li>
          );
        })}
      </ul>
    </div>
  );
}

export default GestionProductos;