import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { iniciarSesion, cerrarSesion } from '../auth/autenticacion';

function Login() {
  const [datos, setDatos] = useState({ username: '', password: '' });
  const history = useHistory();
  
  const manejarCambio = (e) => {
    setDatos({ ...datos, [e.target.name]: e.target.value });
  };

  const manejarEnvio = (e) => {
    e.preventDefault();
    iniciarSesion(datos).then((res) => {
      history.push('/clientes');
    });
  };

  const manejarCerrarSesion = () => {
    cerrarSesion();
    history.push('/');
  };

  return (
    <form onSubmit={manejarEnvio}>
      <input type="text" name="username" onChange={manejarCambio} />
      <input type="password" name="password" onChange={manejarCambio} />
      <button type="submit">Iniciar sesión</button>
      <button type="button" onClick={manejarCerrarSesion}>Cerrar sesión</button>
    </form>
  );
}

export default Login;