// src/paginas/Registro.js
import React, { useState } from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';

function Registro() {
  const [registroForm, setRegistroForm] = useState({
    nombreUsuario: '',
    email: '',
    password: '',
  });
  const history = useHistory();

  const manejarCambio = (e) => {
    setRegistroForm({ ...registroForm, [e.target.name]: e.target.value });
  };

  const manejarRegistro = (e) => {
    e.preventDefault();

    axios
      .post('https://node-js-ac2.rj.r.appspot.com/clientes/register', registroForm)
      .then((res) => {
        if (res.status === 201) {
          alert('Registro exitoso! Por favor inicie sesión.');
          history.push('/');
        } else {
          alert('Hubo un problema con el registro. Por favor, inténtalo de nuevo.');
        }
      })
      .catch((err) => {
        alert('Error al registrarse. Por favor, inténtalo de nuevo.');
      });
  };

  return (
    <div>
      <h1>Registro</h1>
      <form onSubmit={manejarRegistro}>
        <div>
          <label htmlFor="username">Nombre de usuario:</label>
          <input
            type="text"
            name="username"
            id="username"
            required
            onChange={manejarCambio}
          />
        </div>
        <div>
          <label htmlFor="email">Email:</label>
          <input
            type="email"
            name="email"
            id="email"
            required
            onChange={manejarCambio}
          />
        </div>
        <div>
          <label htmlFor="password">Contraseña:</label>
          <input
            type="password"
            name="password"
            id="password"
            required
            onChange={manejarCambio}
          />
        </div>
        <button type="submit">Registrarse</button>
      </form>
    </div>
  );
}

export default Registro;